import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { injectIntl, intlShape } from 'react-intl';
import Participators from '../Participators';
import AutoComplete from '../AutoComplete';
import HandleErrors from '../HandleErrors';
import addLabel from '../../HOC/addLabel';

const Dropdown = ({
  parti—ÅipantsError,
  capacityError,
  maxVisitors,
  intl: { formatMessage },
  roomIsSelected,
}) => {
  const participantsErrorText = formatMessage({ id: 'meeting.dropdown.participantsErrorText' });
  const capacityErrorText = formatMessage({
    id: 'meeting.dropdown.capacityErrorText',
  }, { maxVisitors });

  return (
    <div>
      <HandleErrors
        message={participantsErrorText}
        isError={parti—ÅipantsError}
      />

      <HandleErrors
        message={capacityErrorText}
        isError={capacityError && roomIsSelected}
      />

      <AutoComplete
        isError={capacityError && (roomIsSelected || parti—ÅipantsError)}
      />

      <Participators
        maxVisitors={maxVisitors}
        roomIsSelected={roomIsSelected}
      />
    </div>
  );
};

Dropdown.propTypes = {
  intl: intlShape.isRequired,
  parti—ÅipantsError: PropTypes.bool.isRequired,
  capacityError: PropTypes.bool.isRequired,
  roomIsSelected: PropTypes.bool.isRequired,
  maxVisitors: PropTypes.number,
};

const mapStateToProps = state => ({
  parti—ÅipantsError: state.errorReducer.parti—ÅipantsError,
  capacityError: state.errorReducer.capacityError,
  roomIsSelected: state.roomsReducer.roomIsSelected,
  maxVisitors: state.eventsReducer.currentEvent.room.maxVisitors,
});

const ConnectedComponent = connect(mapStateToProps)(injectIntl(Dropdown));

export default addLabel(ConnectedComponent);
