import { time } from 'uniqid';
import { ADD_EVENT } from '../../consts/eventsReducer';
import { TRIGGER_SUCCESS_MODAL } from '../../consts/pageReducer';
import addLocalStorageEvent from '../../db/queries/addLocalStorageEvent';
import triggerModal from '../pageReducer/triggerModal';

const createEventActionCreator = id => ({
  type: ADD_EVENT,
  id,
});

const createEvent = (event) => {
  const id = time();

  return (dispatch) => {
    addLocalStorageEvent(event, id);
    dispatch(createEventActionCreator(id));
    dispatch(triggerModal(TRIGGER_SUCCESS_MODAL));
  };
};

export default createEvent;
