import React from 'react';
import PropTypes from 'prop-types';
import { enableUniqueIds } from 'react-html-id';
import Headline from '../../ui/Headline';
import getDisplayName from '../getDisplayName';
import './addLabel.scss';

const addLabel = (Component) => {
  class AddLabel extends React.Component {
    static propTypes = {
      className: PropTypes.string,
      label: PropTypes.string,
    }

    static defaultProps = {
      className: '',
      label: '',
    }

    constructor(props) {
      super(props);
      enableUniqueIds(this);
    }

    render() {
      const { className, label, ...otherProps } = this.props;

      /* eslint-disable jsx-a11y/label-has-for */

      return (
        <div className={className}>
          <label htmlFor={this.nextUniqueId()}>
            <Headline className="label__headline">{label}</Headline>
          </label>

          <Component {...otherProps} id={this.lastUniqueId()} />
        </div>
      );
    }
  }

  const componentName = getDisplayName(AddLabel);

  AddLabel.displayName = `AddLabel(${componentName})`;

  return AddLabel;
};

export default addLabel;
