import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import ParticipatorCard from '../../ui/ParticipatorCard';
import removeUser from '../../../action-creators/eventsReducer/removeUser';
import updateFreeUsers from '../../../action-creators/dropdownReducer/updateFreeUsers';
import checkForParticipantsError from '../../../action-creators/errorReducer/checkForParticipantsError';
import checkForCapacityError from '../../../action-creators/errorReducer/checkForCapacityError';
import { usersShape, timeShape, dateShape } from '../../../intl/shapes';

class Participators extends Component {
  static propTypes = {
    users: usersShape.isRequired,
    participators: usersShape.isRequired,
    removeUser: PropTypes.func.isRequired,
    updateFreeUsers: PropTypes.func.isRequired,
    checkForParticipantsError: PropTypes.func.isRequired,
    checkForCapacityError: PropTypes.func.isRequired,
    roomIsSelected: PropTypes.bool.isRequired,
    maxVisitors: PropTypes.number,
    currentTime: timeShape,
    editing: PropTypes.bool.isRequired,
    currentDate: dateShape,
  }

  componentDidMount() {
    const {
      updateFreeUsers, users, participators, currentTime, editing, currentDate,
    } = this.props;

    updateFreeUsers(users, participators, currentTime, currentDate, editing);
  }

  componentDidUpdate() {
    const {
      checkForParticipantsError,
      roomIsSelected,
      checkForCapacityError,
      participators,
      maxVisitors,
      updateFreeUsers,
      users,
      currentTime,
      editing,
      currentDate,
    } = this.props;

    updateFreeUsers(users, participators, currentTime, currentDate, editing);

    checkForParticipantsError(participators.length);

    if (roomIsSelected) {
      checkForCapacityError(participators.length, maxVisitors);
    }
  }

  renderParticipators = () => {
    const { participators, removeUser } = this.props;

    return participators.map(user => (
      <ParticipatorCard
        key={user.id}
        name={user.login}
        avatarUrl={user.avatarUrl}
        removeUser={() => {
          removeUser(user.id);
        }}
      />
    ));
  }

  render() {
    return (
      <div>
        {this.renderParticipators()}
      </div>
    );
  }
}

const mapStateToProps = state => ({
  users: state.usersReducer.users,
  participators: state.eventsReducer.currentEvent.users,
  currentTime: state.eventsReducer.currentEvent.time,
  currentDate: state.eventsReducer.currentEvent.date,
  editing: state.pageReducer.editEventModalOpen,
});

const mapDispatchToProps = {
  updateFreeUsers,
  removeUser,
  checkForParticipantsError,
  checkForCapacityError,
};

export default connect(mapStateToProps, mapDispatchToProps)(Participators);
