const increaseTime = (timeStr, subject, lowBorder, highBorder, step) => {
  let time = Number(timeStr);

  if (time < highBorder && time >= lowBorder) {
    time += step;
  } else {
    time = lowBorder;
  }

  time = time.toString();

  if (time < 10) {
    time = `0${time}`;
  }

  return {
    type: subject,
    payload: time,
  };
};

export default increaseTime;
