import { UPDATE_USER_BUSY_TIME } from '../../consts/usersReducer';
import addUserBusyTime from '../../db/queries/addUserBusyTime';

const updateUserBusyTimeActionCreator = (users, busyTime) => ({
  type: UPDATE_USER_BUSY_TIME,
  payload: {
    users,
    busyTime,
  },
});

const updateUserBusyTime = (users, busyTime) => (
  (dispatch) => {
    addUserBusyTime(users, busyTime);
    dispatch(updateUserBusyTimeActionCreator(users, busyTime));
  }
);

export default updateUserBusyTime;
