import { TIME_ERROR } from '../../consts/errorReducer';

const allFieldsToNumber = (obj) => {
  const newObj = {};

  Object.keys(obj).forEach((key) => {
    newObj[key] = Number(obj[key]);
  });

  return newObj;
};


const checkForTimeError = (time) => {
  const {
    startingHours, endingHours, startingMinutes, endingMinutes,
  } = allFieldsToNumber(time);

  return {
    type: TIME_ERROR,
    isError: startingHours > endingHours || (
      startingHours === endingHours
      && startingMinutes > endingMinutes
    ),
  };
};

export default checkForTimeError;
