import deleteLocalStorageEvent from './deleteLocalStorageEvent';
import addLocalStorageEvent from './addLocalStorageEvent';

const updateLocalStorageEvent = (event, id) => (
  new Promise((res) => {
    res(deleteLocalStorageEvent(id));
  })
    .then(() => {
      addLocalStorageEvent(event, id);
    })
);

export default updateLocalStorageEvent;
