import React, { Component } from 'react';
import { connect } from 'react-redux';
import { injectIntl, intlShape } from 'react-intl';
import PropTypes from 'prop-types';
import onClickOutside from 'react-onclickoutside';
import setFocused from '../../../action-creators/dropdownReducer/setFocused';
import Input from '../AutoCompleteInput';
import Menu from '../AutoCompleteMenu';
import './AutoComplete.scss';
import { usersShape } from '../../../intl/shapes';

class AutoComplete extends Component {
  static propTypes = {
    intl: intlShape.isRequired,
    focused: PropTypes.bool.isRequired,
    isError: PropTypes.oneOfType([
      PropTypes.bool,
      PropTypes.number, // 1 or 0
    ]).isRequired,
    setFocused: PropTypes.func.isRequired,
    freeUsers: usersShape.isRequired,
    inputValue: PropTypes.string.isRequired,
  }

  findUsers = () => {
    const { freeUsers, inputValue } = this.props;
    const isItemMatching = (login, value) => login.toLowerCase().includes(
      value.trim().toLowerCase(),
    );

    return freeUsers.filter(user => isItemMatching(user.login, inputValue));
  }

  handleClickOutside = () => {
    const { focused, setFocused } = this.props;

    if (focused) {
      setFocused(false);
    }
  }

  render() {
    const { focused, intl: { formatMessage }, isError } = this.props;
    const placeholder = formatMessage({ id: 'meeting.dropdown.placeholder' });
    const foundUsers = this.findUsers();

    return (
      <div
        className="autocomplete"
        role="combobox" // eslint-disable-line jsx-a11y/role-has-required-aria-props
        aria-expanded={focused}
        aria-haspopup="listbox"
      >
        <Input
          focused={focused}
          placeholder={placeholder}
          isError={isError}
          foundUsers={foundUsers}
        />

        {focused
          && <Menu foundUsers={foundUsers} />
        }
      </div>
    );
  }
}

const mapStateToProps = state => ({
  focused: state.dropdownReducer.focused,
  freeUsers: state.dropdownReducer.freeUsers,
  inputValue: state.dropdownReducer.inputValue,
  currentTime: state.eventsReducer.currentEvent.time,
});

const mapDispatchToProps = {
  setFocused,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(injectIntl(onClickOutside(AutoComplete)));
