import getLocalStorageItems from './getLocalStorageItems';

const addUserBusyTime = (updatedUsers, busyTime) => {
  getLocalStorageItems('users')
    .then(users => (
      users.map((user) => {
        const shouldUpdate = updatedUsers.some(updatedUser => (
          updatedUser.id === user.id
        ));

        if (shouldUpdate) {
          return {
            ...user,
            busyTime: [
              ...user.busyTime,
              busyTime,
            ],
          };
        }

        return user;
      })
    ))
    .then((newUsers) => {
      localStorage.setItem('users', JSON.stringify(newUsers));
    });
};

export default addUserBusyTime;
