import React from 'react';
import FontAwesome from 'react-fontawesome';
import PropTypes from 'prop-types';
import { injectIntl, intlShape } from 'react-intl';
import Paragraph from '../Paragraph';
import './ParticipatorCard.scss';

const ParticipatorCard = ({
  avatarUrl,
  name,
  removeUser,
  intl: {
    formatMessage,
  },
}) => {
  const label = formatMessage({ id: 'participatorCard.label' }, { name });

  return (
    <button
      className="participator-card"
      onClick={removeUser}
      aria-label={label}
    >
      <div className="particitapor-card__image-container">
        <img className="participator-card__avatar" src={avatarUrl} alt={`${name}'s avatar`} />
      </div>
      <Paragraph className="participator-card__paragraph">{name}</Paragraph>
      <div className="participator-card__close-icon">
        <FontAwesome
          name="times"
        />
      </div>
    </button>
  );
};


ParticipatorCard.propTypes = {
  intl: intlShape.isRequired,
  avatarUrl: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  removeUser: PropTypes.func.isRequired,
};

export default injectIntl(ParticipatorCard);
