import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import Input from '../../ui/Input';
import setFocused from '../../../action-creators/dropdownReducer/setFocused';
import changeIndex from '../../../action-creators/dropdownReducer/changeIndex';
import onInputValueChange from '../../../action-creators/dropdownReducer/onInputValueChange';
import { usersShape } from '../../../intl/shapes';
import addUser from '../../../action-creators/eventsReducer/addUser';

class AutoCompleteInput extends Component {
  static propTypes = {
    placeholder: PropTypes.string,
    activeId: PropTypes.string.isRequired,
    isError: PropTypes.oneOfType([
      PropTypes.bool,
      PropTypes.number, // 1 or 0
    ]).isRequired,
    setFocused: PropTypes.func.isRequired,
    highlightedIndex: PropTypes.number.isRequired,
    scrollbar: PropTypes.object.isRequired, // eslint-disable-line react/forbid-prop-types
    addUser: PropTypes.func.isRequired,
    onInputValueChange: PropTypes.func.isRequired,
    inputValue: PropTypes.string.isRequired,
    changeIndex: PropTypes.func.isRequired,
    isAllErrorsShown: PropTypes.bool.isRequired,
    foundUsers: usersShape.isRequired,
  }

  static defaultProps = {
    placeholder: '',
  }

  resetIndex = () => {
    const { changeIndex } = this.props;

    changeIndex(0);
  }

  handleEnter = () => {
    const { addUser, foundUsers, highlightedIndex } = this.props;

    if (foundUsers.length !== 0) {
      addUser(foundUsers[highlightedIndex]);

      if (foundUsers.length - 1 === highlightedIndex) {
        this.resetIndex();
      }
    }
  }

  handleArrowUp = () => {
    const {
      foundUsers, highlightedIndex, changeIndex, scrollbar,
    } = this.props;

    if (highlightedIndex > 0) {
      changeIndex(highlightedIndex - 1);
      scrollbar.scrollTop(35 * (highlightedIndex - 1));
    } else {
      changeIndex(foundUsers.length - 1);
      scrollbar.scrollToBottom();
    }
  }

  handleArrowDown = () => {
    const {
      foundUsers, highlightedIndex, changeIndex, scrollbar,
    } = this.props;

    if (highlightedIndex <= foundUsers.length - 2) {
      changeIndex(highlightedIndex + 1);
      scrollbar.scrollTop(35 * highlightedIndex);
    } else {
      this.resetIndex();
      scrollbar.scrollToTop();
    }
  }

  listenToKeypresses = (e) => {
    const { setFocused } = this.props;

    switch (e.key) {
      case 'Enter':
        e.preventDefault();
        this.handleEnter();
        break;

      case 'ArrowUp':
        e.preventDefault();
        this.handleArrowUp();
        break;

      case 'ArrowDown':
        e.preventDefault();
        this.handleArrowDown();
        break;

      case 'Escape':
        e.preventDefault();
        e.target.blur();
        setFocused();
        break;

      case 'Tab':
        setFocused();
        break;

      default:
        break;
    }
  }

  onChange = (e) => {
    const { onInputValueChange } = this.props;

    this.resetIndex();
    e.preventDefault();
    onInputValueChange(e.target.value);
  }

  render() {
    const {
      placeholder, activeId, isError, setFocused, inputValue, isAllErrorsShown,
    } = this.props;

    return (
      <Input
        onChange={this.onChange}
        value={inputValue}
        onKeyDown={this.listenToKeypresses}
        placeholder={placeholder}
        aria-autocomplete="both"
        aria-activedescendant={activeId}
        isError={isError && isAllErrorsShown}
        onFocus={() => setFocused(true)}
      />
    );
  }
}

const mapStateToProps = state => ({
  highlightedIndex: state.dropdownReducer.highlightedIndex,
  isAllErrorsShown: state.errorReducer.isAllErrorsShown,
  inputValue: state.dropdownReducer.inputValue,
  activeId: state.dropdownReducer.activeId,
  scrollbar: state.dropdownReducer.scrollbar,
});

const mapDispatchToProps = {
  setFocused,
  changeIndex,
  onInputValueChange,
  addUser,
};

export default connect(mapStateToProps, mapDispatchToProps)(AutoCompleteInput);
