import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { injectIntl, intlShape } from 'react-intl';
import TimeInput from '../TimeInput';
import updateTime from '../../../action-creators/eventsReducer/hoursActions/updateTime';
import resetTime from '../../../action-creators/eventsReducer/hoursActions/resetTime';
import increaseTime from '../../../action-creators/eventsReducer/hoursActions/increaseTime';
import decreaseTime from '../../../action-creators/eventsReducer/hoursActions/decreaseTime';
import checkForTimeError from '../../../action-creators/errorReducer/checkForTimeError';
import checkForLimitError from '../../../action-creators/errorReducer/checkForLimitError';
import {
  UPDATE_STARTING_HOURS,
  UPDATE_ENDING_HOURS,
  UPDATE_STARTING_MINUTES,
  UPDATE_ENDING_MINUTES,
} from '../../../consts/eventsReducer';
import { timeShape } from '../../../intl/shapes';
import './TimeSetter.scss';

class TimeSetter extends Component {
  static propTypes = {
    intl: intlShape.isRequired,
    time: timeShape.isRequired,
    increaseTime: PropTypes.func.isRequired,
    decreaseTime: PropTypes.func.isRequired,
    updateTime: PropTypes.func.isRequired,
    resetTime: PropTypes.func.isRequired,
    checkForTimeError: PropTypes.func.isRequired,
    checkForLimitError: PropTypes.func.isRequired,
  }

  componentDidUpdate() {
    const {
      checkForTimeError,
      checkForLimitError,
      time,
    } = this.props;

    checkForLimitError(time.startingHours, 8);
    checkForTimeError(time);
  }

  onChangeTime = (subject, value, maximumValue) => {
    const inputValue = Number(value);
    const { updateTime, resetTime } = this.props;
    const shouldReset = !Number.isNaN(inputValue);

    if (shouldReset) {
      if (value.length > 2 || inputValue > maximumValue) {
        resetTime(subject);
      } else {
        updateTime(value, subject);
      }
    }
  }

  render() {
    const
      {
        time,
        increaseTime,
        decreaseTime,
        intl: { formatMessage },
      } = this.props;

    const startLabel = formatMessage({ id: 'meeting.timeSetter.startLabel' });
    const endLabel = formatMessage({ id: 'meeting.timeSetter.endLabel' });

    return (
      <div className="time-setter">
        <TimeInput
          label={startLabel}
          className="time-setter__input"
          hours={time.startingHours}
          minutes={time.startingMinutes}
          increaseHours={() => {
            increaseTime(time.startingHours, UPDATE_STARTING_HOURS, 8, 22, 1);
          }}
          increaseMinutes={() => {
            increaseTime(time.startingMinutes, UPDATE_STARTING_MINUTES, 0, 55, 5);
          }}
          decreaseHours={() => {
            decreaseTime(time.startingHours, UPDATE_STARTING_HOURS, 9, 1);
          }}
          decreaseMinutes={() => {
            decreaseTime(time.startingMinutes, UPDATE_STARTING_MINUTES, 5, 5);
          }}
          onHoursChange={(e) => {
            this.onChangeTime(UPDATE_STARTING_HOURS, e.target.value, 22);
          }}
          onMinutesChange={(e) => {
            this.onChangeTime(UPDATE_STARTING_MINUTES, e.target.value, 59);
          }}
        />

        <span className="time-setter__space">‚ñ¨</span>

        <TimeInput
          label={endLabel}
          className="time-setter__input"
          hours={time.endingHours}
          minutes={time.endingMinutes}
          increaseHours={() => {
            increaseTime(time.endingHours, UPDATE_ENDING_HOURS, 8, 22, 1);
          }}
          increaseMinutes={() => {
            increaseTime(time.endingMinutes, UPDATE_ENDING_MINUTES, 0, 55, 5);
          }}
          decreaseHours={() => {
            decreaseTime(time.endingHours, UPDATE_ENDING_HOURS, 9, 1);
          }}
          decreaseMinutes={() => {
            decreaseTime(time.endingMinutes, UPDATE_ENDING_MINUTES, 5, 5);
          }}
          onHoursChange={(e) => {
            this.onChangeTime(UPDATE_ENDING_HOURS, e.target.value, 22);
          }}
          onMinutesChange={(e) => {
            this.onChangeTime(UPDATE_ENDING_MINUTES, e.target.value, 59);
          }}
        />
      </div>
    );
  }
}

const mapStateToProps = state => ({
  time: state.eventsReducer.currentEvent.time,
});

const mapDispatchToProps = {
  updateTime,
  resetTime,
  increaseTime,
  decreaseTime,
  checkForTimeError,
  checkForLimitError,
};

export default connect(mapStateToProps, mapDispatchToProps)(injectIntl(TimeSetter));
