import React, { forwardRef } from 'react';
import PropTypes from 'prop-types';
import './Index.scss';

// DateSetterComponent makes use of ref with this component

const Input = forwardRef(({ isError, className, ...otherProps }, ref) => (
  <input
    type="text"
    className={`input ${isError ? 'input--error' : ''} ${className}`}
    ref={ref}
    {...otherProps}
  />
));

Input.propTypes = {
  isError: PropTypes.bool,
  className: PropTypes.string,
};

Input.defaultProps = {
  isError: false,
  className: '',
};

export default Input;
