import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { injectIntl, intlShape } from 'react-intl';
import DateSetterInput from '../DateSetterInput';
import TimeSetter from '../TimeSetter';
import './DateAndTime.scss';

class DateAndTime extends Component {
  static propTypes = {
    intl: intlShape.isRequired,
    className: PropTypes.string,
  }

  static defaultProps = {
    className: '',
  }

  containerStyles = {
    display: 'flex',
  }

  render() {
    const { className, intl: { formatMessage } } = this.props;
    const dateLabel = formatMessage({ id: 'meeting.timeSetter.dateLabel' });

    return (
      <div className={`date-and-time ${className}`}>
        <DateSetterInput
          label={dateLabel}
        />
        <TimeSetter />
      </div>
    );
  }
}

export default injectIntl(DateAndTime);
