import React from 'react';
import PropTypes from 'prop-types';
import './GreyBtn.scss';

const GreyBtn = ({ children, className, ...otherProps }) => (
  <button className={`grey-btn ${className}`} {...otherProps}>{children}</button>
);

GreyBtn.propTypes = {
  children: PropTypes.string.isRequired,
  className: PropTypes.string,
};

GreyBtn.defaultProps = {
  className: '',
};

export default GreyBtn;
