import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { injectIntl, intlShape } from 'react-intl';
import RoomCard from '../../ui/RoomCard';
import Headline from '../../ui/Headline';
import triggerSelected from '../../../action-creators/roomsReducer/triggerSelected';
import setRoom from '../../../action-creators/eventsReducer/setRoom';
import resetRoom from '../../../action-creators/eventsReducer/resetRoom';
import checkForRoomError from '../../../action-creators/errorReducer/checkForRoomError';
import {
  usersShape, eventsShape, roomsShape, timeShape, workingTimeShape, dateShape,
} from '../../../intl/shapes';
import HandleErrors from '../HandleErrors';
import './RoomCards.scss';

class RoomCards extends Component {
  static propTypes = {
    intl: intlShape.isRequired,
    rooms: roomsShape.isRequired,
    events: eventsShape.isRequired,
    roomIsSelected: PropTypes.bool.isRequired,
    currentEventTime: timeShape.isRequired,
    currentEventUsers: usersShape.isRequired,
    roomError: PropTypes.bool.isRequired,
    checkForRoomError: PropTypes.func.isRequired,
    resetRoom: PropTypes.func.isRequired,
    closeTime: workingTimeShape.isRequired,
    openTime: workingTimeShape.isRequired,
    triggerSelected: PropTypes.func.isRequired,
    editing: PropTypes.bool.isRequired,
    setRoom: PropTypes.func.isRequired,
    className: PropTypes.string,
    date: dateShape.isRequired,
    currentEventId: PropTypes.string,
  }

  static defaultProps = {
    className: '',
  }

  componentDidUpdate() {
    const { checkForRoomError, roomIsSelected } = this.props;

    checkForRoomError(roomIsSelected);
  }

  getThisRoomEventsTime = (room) => {
    const {
      editing, currentEventId, date, events,
    } = this.props;

    const deleteNulls = arr => arr.filter(elem => elem !== null);

    const neededEvents = events.filter(event => event.date.startingDate === date.startingDate
      && event.date.startingMonth === date.startingMonth);

    const freeTime = neededEvents.map((event) => {
      if (event.room.id === room.id) {
        if (editing) {
          return event.id !== currentEventId ? event.time : null;
        }

        return event.time;
      }

      return null;
    });

    return deleteNulls(freeTime);
  }

  sortEventsByTime = (events) => {
    if (events.length < 2) {
      return events;
    }

    const pivotPosition = Math.floor(Math.random() * events.length);
    const pivot = events[pivotPosition];
    const less = events.filter(elem => (
      elem.endingHours <= pivot.startingHours && elem.endingMinutes <= pivot.startingMinutes
    ));

    const greater = events.filter(elem => (
      elem.startingHours >= pivot.endingHours && elem.startingMinutes >= pivot.endingMinutes
    ));

    return [...this.sortEventsByTime(less), pivot, ...this.sortEventsByTime(greater)];
  }

  getConditions = (
    startingHours1,
    startingMinutes1,
    startingHours2,
    startingMinutes2,
    endingHours1,
    endingMinutes1,
    endingHours2,
    endingMinutes2,
  ) => {
    const firstCondition = startingHours1 > endingHours1
      || (startingHours1 == endingHours1 // eslint-disable-line eqeqeq
      && startingMinutes1 >= endingMinutes1);

    const secondCondition = startingHours2 > endingHours2
      || (startingHours2 == endingHours2 // eslint-disable-line eqeqeq
      && startingMinutes2 >= endingMinutes2);

    return firstCondition && secondCondition;
  }

  getFreeTime = (room) => {
    const {
      currentEventTime,
      openTime,
      closeTime,
    } = this.props;

    const thisRoomEvents = this.getThisRoomEventsTime(room);

    if (thisRoomEvents.length < 1) {
      return {
        startingHours: openTime.hours,
        startingMinutes: openTime.minutes,
        endingHours: closeTime.hours,
        endingMinutes: closeTime.minutes,
      };
    }

    const sortedTimeArr = this.sortEventsByTime(thisRoomEvents);

    for (let i = 0; i < sortedTimeArr.length; i++) {
      let startingHours;
      let startingMinutes;
      let endingHours;
      let endingMinutes;

      const isEventFirst = this.getConditions(
        currentEventTime.startingHours,
        currentEventTime.startingMinutes,
        sortedTimeArr[i].startingHours,
        sortedTimeArr[i].startingMinutes,
        openTime.hours,
        openTime.minutes,
        currentEventTime.endingHours,
        currentEventTime.endingMinutes,
      );

      const isEventLast = this.getConditions(
        currentEventTime.startingHours,
        currentEventTime.startingMinutes,
        closeTime.hours,
        closeTime.minutes,
        sortedTimeArr[i].endingHours,
        sortedTimeArr[i].endingMinutes,
        currentEventTime.endingHours,
        currentEventTime.endingMinutes,
      );

      const isEventMidst = sortedTimeArr[i + 1] ? this.getConditions(
        currentEventTime.startingHours,
        currentEventTime.startingMinutes,
        sortedTimeArr[i + 1].startingHours,
        sortedTimeArr[i + 1].startingMinutes,
        sortedTimeArr[i].endingHours,
        sortedTimeArr[i].endingMinutes,
        currentEventTime.endingHours,
        currentEventTime.endingMinutes,
      ) : false;

      if (isEventFirst
        && !sortedTimeArr[i - 1]
      ) {
        startingHours = openTime.hours < 10 ? `0${openTime.hours}` : openTime.hours;
        startingMinutes = openTime.minutes;
        endingHours = sortedTimeArr[i].startingHours;
        endingMinutes = sortedTimeArr[i].startingMinutes;
      }

      if (isEventLast
        && !sortedTimeArr[i + 1]
      ) {
        startingHours = sortedTimeArr[i].endingHours;
        startingMinutes = sortedTimeArr[i].endingMinutes;
        endingHours = closeTime.hours;
        endingMinutes = closeTime.minutes;
      }

      if (isEventMidst) {
        startingHours = sortedTimeArr[i].endingHours;
        startingMinutes = sortedTimeArr[i].endingMinutes;
        endingHours = sortedTimeArr[i + 1].startingHours;
        endingMinutes = sortedTimeArr[i + 1].startingMinutes;
      }

      if (startingHours !== undefined) { // Without this check, shows undefined time in component.
        return { // If it fails, nothing renders.
          startingHours,
          startingMinutes,
          endingHours,
          endingMinutes,
        };
      }
    }
  }

  renderSelectedRoom = () => {
    const { rooms, resetRoom, triggerSelected } = this.props;

    const selectedRoom = rooms.find(room => room.selected);

    const freeTime = this.getFreeTime(selectedRoom);

    return (
      freeTime ? (
        <RoomCard
          key={selectedRoom.id}
          freeTime={freeTime}
          roomName={selectedRoom.name}
          floorNumber={selectedRoom.floor}
          selected={selectedRoom.selected}
          onCardDeselect={(e) => {
            e.stopPropagation();
            triggerSelected(selectedRoom.id);
            resetRoom();
          }}
        />
      ) : null
    );
  }

  getRoomsToRender = () => {
    const { rooms, currentEventUsers } = this.props;

    const neededRooms = rooms.filter((room) => {
      const shouldRoomBeRecommended = () => currentEventUsers.length <= room.maxVisitors;

      return shouldRoomBeRecommended ? room : null;
    });

    if (currentEventUsers.length > 0) {
      const floors = currentEventUsers.map(user => user.homeFloor);

      const sortRooms = (rooms, compared) => {
        if (rooms.length < 2) {
          return rooms;
        }

        const equalToCompared = rooms.filter(room => room.floor === compared);
        const greater = rooms.filter(room => room.floor > compared);
        const less = rooms.filter(room => room.floor < compared);

        return [...equalToCompared, ...less, ...greater];
      };

      const neededFloor = () => {
        const sortedFloors = floors.sort();
        let highestOccurence = sortedFloors[0];
        let count = 0;
        let newCount = 1;

        for (let i = 0; i < sortedFloors.length; i++) {
          if (sortedFloors[i] === highestOccurence) {
            count++;
          } else if (sortedFloors[i] === sortedFloors[i + 1]) {
            newCount++;
            if (newCount > count) {
              highestOccurence = sortedFloors[i];
              count = newCount;
              newCount = 1;
            }
          }
        }

        return highestOccurence;
      };

      return sortRooms(neededRooms, neededFloor(floors));
    }

    return neededRooms;
  }

  renderRooms = () => {
    const { setRoom, triggerSelected } = this.props;
    const roomsToRender = this.getRoomsToRender();
    let overallRooms = 0;

    return roomsToRender.map((room) => {
      const freeTime = this.getFreeTime(room);

      if (freeTime && overallRooms < 5) {
        overallRooms++;
        return (
          <RoomCard
            key={room.id}
            freeTime={freeTime}
            roomName={room.name}
            floorNumber={room.floor}
            selected={room.selected}
            onCardSelect={() => {
              setRoom(room);
              triggerSelected(room.id);
            }}
          />
        );
      }

      return null;
    });
  }

  render() {
    const {
      roomIsSelected, className, roomError, intl: { formatMessage },
    } = this.props;
    const selectedHeadline = formatMessage({ id: 'meeting.roomCards.selectedHeadline' });
    const unselectedHeadline = formatMessage({ id: 'meeting.roomCards.unselectedHeadline' });
    const errorMessage = formatMessage({ id: 'meeting.roomCards.errorText' });

    return (
      <section className={`room-cards ${className}`}>
        <Headline className="room-cards__headline" tabIndex="0">
          {roomIsSelected ? selectedHeadline : unselectedHeadline}
        </Headline>

        <HandleErrors
          message={errorMessage}
          isError={roomError}
        />

        <div>
          {roomIsSelected ? this.renderSelectedRoom() : this.renderRooms()}
        </div>
      </section>
    );
  }
}


const mapStateToProps = state => ({
  rooms: state.roomsReducer.rooms,
  roomIsSelected: state.roomsReducer.roomIsSelected,
  roomError: state.errorReducer.roomError,
  events: state.eventsReducer.events,
  currentEventTime: state.eventsReducer.currentEvent.time,
  currentEventUsers: state.eventsReducer.currentEvent.users,
  closeTime: state.pageReducer.closeTime,
  openTime: state.pageReducer.openTime,
  editing: state.pageReducer.editEventModalOpen,
  currentEventId: state.eventsReducer.currentEvent.id,
  date: state.eventsReducer.currentEvent.date,
});

const mapDispatchToProps = {
  triggerSelected,
  setRoom,
  resetRoom,
  checkForRoomError,
};

export default connect(mapStateToProps, mapDispatchToProps)(injectIntl(RoomCards));
