import React, { Component } from 'react';
import PropTypes from 'prop-types';
import './DeleteBtn.scss';

export default class DeleteBtn extends Component {
  static propTypes = {
    text: PropTypes.string.isRequired,
    onClick: PropTypes.func.isRequired,
    className: PropTypes.string,
  }

  static defaultProps = {
    className: '',
  }

  mobileDeleteBtnStyles = {
    width: '100%',
    backgroundColor: '#fff',
    color: '#ff3333',
    textAlign: 'center',
    height: '60px',
    fontFamily: 'TextBook, sans-serif',
    fontWeight: 'normal',
    fontSize: '17px',
    cursor: 'pointer',
    border: 'none',
    transition: '.15s ease-in-out',
    outline: 'none',
    ':focus,:active': {
      color: '#f00',
    },
  }

  render() {
    const { text, onClick, className } = this.props;

    return (
      <button className={`delete-btn ${className}`} onClick={onClick}>
        {text}
      </button>
    );
  }
}
