import React, { Component } from 'react';
import { connect } from 'react-redux';
import Scrollbar from 'react-custom-scrollbars';
import PropTypes from 'prop-types';
import DropdownOption from '../../ui/DropdownOption';
import getScrollbar from '../../../action-creators/dropdownReducer/getScrollbar';
import './AutoCompleteMenu.scss';
import addUser from '../../../action-creators/eventsReducer/addUser';
import { usersShape } from '../../../intl/shapes';

class AutoCompleteMenu extends Component {
  static propTypes = {
    addUser: PropTypes.func.isRequired,
    getScrollbar: PropTypes.func.isRequired,
    foundUsers: usersShape.isRequired,
  }

  componentDidMount() {
    const { getScrollbar } = this.props;

    getScrollbar(this.scrollbars);
  }

  renderItems = () => {
    const { foundUsers, highlightedIndex, addUser, activeId } = this.props;

    return foundUsers.map((user, index) => {
      const isHighlighted = highlightedIndex === index;
      const onClick = () => addUser(user);

      return (
        <DropdownOption
          key={user.id}
          name={user.login}
          homeFloor={user.homeFloor}
          avatarSrc={user.avatarUrl}
          highlighted={isHighlighted}
          onClick={onClick}
          id={isHighlighted ? activeId : null}
        />
      );
    });
  }

  scrollbarTrackVertical = ({ style, props }) => {
    const { noScrollbar } = this.props;

    const trackStyles = !noScrollbar
      ? {
        display: 'inline-block',
        height: '100%',
        backgroundColor: '#ffffff',
        width: '22px',
        right: '0px',
        top: '0px',
        boxSizing: 'border-box',
        paddingBottom: '10px',
      }
      : { display: 'none' };

    return (
      <div
        style={{
          ...style,
          ...trackStyles,
        }}
        {...props}
      />
    );
  }

  scrollbarThumb = ({ style, props }) => {
    const { noScrollbar } = this.props;

    const thumbStyles = !noScrollbar
      ? {
        width: '11px',
        backgroundColor: '#d5dfe9',
        borderRadius: '5px',
        marginTop: '5px',
        marginLeft: '5px',
      }
      : { display: 'none' };

    return (
      <div
        style={{
          ...style,
          ...thumbStyles,
        }}
        {...props}
      />
    );
  }

  render() {
    return (
      <div
        className="autocomplete__menu"
        aria-selected="true"
      >
        <Scrollbar
          autoHeight
          autoHeightMax={140}
          renderThumbVertical={this.scrollbarThumb}
          renderTrackVertical={this.scrollbarTrackVertical}
          hideTracksWhenNotNeeded
          ref={(s) => { this.scrollbars = s; }}
        >
          {
            this.renderItems()
          }
        </Scrollbar>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  activeId: state.dropdownReducer.activeId,
  freeUsers: state.dropdownReducer.freeUsers,
  highlightedIndex: state.dropdownReducer.highlightedIndex,
  inputValue: state.dropdownReducer.inputValue,
});

const mapDispatchToProps = {
  getScrollbar,
  addUser,
};

export default connect(mapStateToProps, mapDispatchToProps)(AutoCompleteMenu);
